import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { InputLogic } from '../../input/inputlogic';
import ExpenseInput from '../../components/ExpenseInput';

const Utilities = () => {

	useEffect(() => {
		InputLogic.focusFirstInputElement()
	}, []);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			<div class="container">
				<hr/>
				<h5>Utilities</h5>
				Not all the expenses here apply to your situation, only check on those item that is applicable. The expense amount can be estimates. Click next when you are done.<p/>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.utilities.energy} title="Electricity/Gas" min_value="0" max_value="10000"
							description="Electricity and gas utilities."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.utilities.water_sewer} title="Water/Sewer" min_value="0" max_value="50000"
							description="Water and sewer usages."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.utilities.phones} title="Cells/Phone" min_value="0" max_value="20000"
							description="Cell phones, land lines etc."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.utilities.internet} title="Internet" min_value="0" max_value="20000"
							description="Cost of Internet via fiber/DSL/cable-modem/starlink/dial-ups."/>					
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.utilities.garbage_recycle} title="Garbage/Recycle" min_value="0" max_value="5000"
							description="Garbage and recycle services."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.utilities.other} title="Other utilities" min_value="0" max_value="20000"
							description="Other utility cost."/>
					</div>
				</div>								
                <hr/>

				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/basic')}>Back: Housing</button>&nbsp;
				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/health')}>Next: Health / Child</button>
				<hr/>                
				<h5>Streaming</h5>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.subscriptions.cable_tv} title="Cable TV" min_value="0" max_value="10000"
							description="Cable TV subscription."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.subscriptions.tv_streaming} title="TV streaming" min_value="0" max_value="50000"
							description="Youtube TV, Netflix, Hulu, HBOMax, Paramount+, ESPN+, Disney+, Apple+ etc"/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.subscriptions.music_streaming} title="Music streaming" min_value="0" max_value="10000"
							description="Spotify, Amazon Music, Google Music etc."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.subscriptions.game_subscription} title="Gaming services" min_value="0" max_value="50000"
							description="Xbox pass, Playstation, Nintendo etc."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.subscriptions.other} title="Other streaming" min_value="0" max_value="10000"
							description="Other streaming related expenses."/>

					</div>
					<div class="col">
					</div>
				</div>
			</div>
		</div>
		
	);
};

export default Utilities;
