import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { InputLogic } from '../../input/inputlogic';
import ExpenseInput from '../../components/ExpenseInput';

const Retailer = () => {

	useEffect(() => {
		InputLogic.focusFirstInputElement()
	}, []);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			<div class="container">
				<hr/>
				<h5>Expense by Retailers</h5>
				Some people prefer not to split everything into detailed categories. Instead most people are buying from big retailers in the country like Amazon, Costco, Walmart, Sam's club, Target etc. If this the way you want to group by, you can enter by retailer. If you prefer to itemize everything by fine category, you can skip to the next section.<p/>
                You should not double expense on item enter here. For eg. if you are buying some groceries from Costco, you should enter just the groceries expense that not from Costco in the other section. Or if you are buying all gifts from Amazon, then don't enter anything in other gifts section.
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.retailer.amazon} title="Amazon / Wholefoods" min_value="0" max_value="100000"
							description="All Amazon purchases."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.retailer.costco} title="Costco" min_value="0" max_value="50000"
							description="All Costco purchases."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.retailer.walmart} title="Walmart" min_value="0" max_value="20000"
							description="All Walmart purchases."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.retailer.sams_club} title="Sam's Club" min_value="0" max_value="20000"
							description="All Sam's club purchases."/>					
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.retailer.target} title="Target" min_value="0" max_value="5000"
							description="All Target purchases."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.retailer.homedepot_lowes} title="Home Improvements" min_value="0" max_value="20000"
							description="Home Depot, Lowe's or other home improvements store."/>
					</div>
				</div>		
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.retailer.pharmacies} title="Pharmacies" min_value="0" max_value="5000"
							description="All pharmacies purchases such as CVS, Walgreen, RiteAid etc."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.retailer.other} title="Other Retailers" min_value="0" max_value="20000"
							description="Other retailers not listed above."/>
					</div>
				</div>
                <hr/>
				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/health')}>Back: Health/Child</button>&nbsp;
				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/food')}>Next: Food / Child</button>
				<hr/>                	                						
			</div>
		</div>
		
	);
};

export default Retailer;
